<template>
  <div class="payChiose">
    <div class="payChiose-container">
      <div class="payChiose-header">
        <div class="payChiose-title">支付</div>
        <div class="payChiose-close iconfont icon-cuowu" @click="cancel()"></div>
      </div>
      <div class="payChiose-body">
        <div class="item-con" v-for="(item, index) in list" :key="index" @click="featuresFun(item)">
          <img class="icon-img" :src="item.icon" alt>
          <span class="text">{{item.payName}}</span>
        </div>
      </div>
    </div>
    <div class="payChiose-bg"></div>
  </div>
</template>
<script>
import { Indicator, Toast, MessageBox } from "mint-ui";
export default {
  props: {
    isManyOrder: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      list: this.isManyOrder
        ? [
            {
              icon: "./static/images/qianbao.png",
              payName: "现金",
              payCode: 1
            },
            {
              icon: "./static/images/yinlian.png",
              payName: "银行卡",
              payCode: 2
            },
            { icon: "./static/images/ka.png", payName: "一卡通", payCode: 4 },

            // {
            //   icon: "./static/images/dianzicanjuan.png",
            //   payName: "餐券",
            //   payCode: 6
            // },
            {
              icon: "./static/images/weixinPay.png",
              payName: "微信支付",
              payCode: 12
            },
            {
              icon: "./static/images/zhifubaoPay.png",
              payName: "支付宝",
              payCode: 13
            }
            // { icon: "./static/images/miandanjuan.png", payName: "免单", payCode: 14 }
          ]
        : [
            {
              icon: "./static/images/qianbao.png",
              payName: "现金",
              payCode: 1
            },
            {
              icon: "./static/images/yinlian.png",
              payName: "银行卡",
              payCode: 2
            },
            { icon: "./static/images/ka.png", payName: "一卡通", payCode: 4 },
            {
              icon: "./static/images/guaqi.png",
              payName: "挂帐",
              payCode: 5
            },
            // {
            //   icon: "./static/images/dianzicanjuan.png",
            //   payName: "餐券",
            //   payCode: 6
            // },
            {
              icon: "./static/images/weixinPay.png",
              payName: "微信支付",
              payCode: 12
            },
            {
              icon: "./static/images/zhifubaoPay.png",
              payName: "支付宝",
              payCode: 13
            }
            // { icon: "./static/images/miandanjuan.png", payName: "免单", payCode: 14 }]
          ]
    };
  },
  created() {},
  methods: {
    cancel() {
      this.$emit("hidePage", "");
    },
    featuresFun(item) {
      // console.log("item", item);
      if (item.payCode == 5) {
        this.$router.push("/foodAccount");
      } else {
        MessageBox.confirm("确认支付？")
          .then(action => {
            this.$emit("hidePage", {
              state: "confirmPay",
              item: item
            });
          })
          .catch(action => {});
      }
    }
  }
};
</script>
<style lang="less" scoped>
.payChiose {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  height: 100%;
  .payChiose-bg {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    opacity: 0.7;
    background: #000000;
  }
  .payChiose-container {
    width: 8rem;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    .payChiose-header {
      height: 1.067rem;
      margin: 0 0.32rem;
      padding: 0 5px;
      border-bottom: 1px solid #dddddd;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .payChiose-title {
        font-size: 0.32rem;
        color: #333333;
      }
    }
    .payChiose-body {
      margin: 0 0.32rem 0.427rem 0.32rem;

      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .item-con {
        width: 3.653rem;
        height: 1.33rem;
        background: #f6f8f9;
        border-radius: 3px;
        margin-top: 0.267rem;
        display: flex;
        align-items: center;
        .icon-img {
          width: 0.67rem;
          margin: 0 0.267rem;
        }
        .text {
          font-size: 0.427rem;
          color: #606a84;
        }
      }
    }
  }
}
</style>



