<template>
  <div class="foodOeder">
    <foodHeaderNav title="订单" :iconText="goTitle" eventShow="true" @hidePageFun="gotoBack()"></foodHeaderNav>
    <div class="nav">
      <div class="num-price">
        <div class="num-content">
          <span class="num-tips">菜品数：</span>
          <span class="num">{{menuInfo.pluCounts}}个</span>
        </div>
        <div class="price-content">
          <span class="price-tips">订单金额：</span>
          <span class="price">
            <span class="price-unit">￥</span>
            {{menuInfo.amount}}
          </span>
        </div>
      </div>
      <div class="title">
        <div class="number">
          {{orderNum}}
          <i class="iconfont icon-jiantou1" @click="gotoInfo()"></i>
        </div>
        <div class="restaurant">{{menuInfo.pcDesc}}</div>
        <div v-if="footShow==1" class="orderStatus waitPay">待支付</div>
        <div v-else-if="footShow==0" class="orderStatus waitCooking">未下厨</div>
        <div v-else-if="footShow==2" class="orderStatus cooking">已下厨</div>
        <div v-else-if="footShow==-1" class="orderStatus failure">下单失败</div>
        <div v-else-if="footShow==-5" class="orderStatus failure">已取消</div>
      </div>
    </div>
    <div class="body">
      <div class="menu-list">
        <div
          class="menu-item"
          v-for="(Menu, index) in menuInfo.pluInfos"
          :key="index"
          @click="editMenuFun(Menu,index)"
        >
          <div class="number">{{index+1}}</div>
          <div class="menu-content">
            <div class="menu-name">{{Menu.descript}}</div>
            <div class="menu-practice">
              <span v-for="(practice,i) in Menu.combomeals" :key="i">
                <span class="menu-practice-chose">{{practice.mealName}}</span>
              </span>
            </div>
          </div>
          <div class="quantity">x {{Menu.number}}</div>
          <div class="price-content">
            <div class="price-unit">￥</div>
            {{Menu.price}} {{footShow.pluSta}}
          </div>

          <i class="iconfont icon-jiantou1" v-if="footShow ==1"></i>
          <i class="iconfont" v-if="footShow ==2||footShow ==-1||footShow ==0"></i>
        </div>
        <!-- <div class="menu-item">
          <div class="number">1</div>
          <div class="menu-content">
            <div class="menu-name">美式咖啡卡dsafdasfsadf布奇洛</div>
            <div class="menu-practice">清蒸</div>
          </div>
          <div class="quantity">x 1</div>
          <div class="price-content">
            <div class="price-unit">￥</div>149.00
          </div>
          <i class="iconfont icon-jiantou1"></i>
        </div>-->
      </div>
    </div>
    <foodPayChoise v-if="isShowPay" @hidePage="hidePage($event)"></foodPayChoise>
    <div class="foot">
      <div class="btn-list" v-if="footShow==1">
        <!-- 未支付-->
        <!-- <div class="btn-item" @click="gotoInfo()">信息</div> -->
        <div class="btn-item" @click="print()">打印</div>
        <div class="btn-item" @click="cancel('directCance')">取消订单</div>
        <div class="btn-item" @click="addMenu()">加菜</div>
        <div class="btn-item" @click="pay()">支付</div>
        <div class="btn-item" @click="gotoIndex()">新开单</div>
      </div>
      <div class="btn-list" v-if="footShow==2">
        <!-- 已下厨 -->
        <!-- <div class="btn-item" @click="gotoInfo()">信息</div> -->
        <div class="btn-item" @click="print()">打印</div>
        <div class="btn-item" @click="gotoIndex()">新开单</div>
      </div>
      <div class="btn-list" v-if="footShow==0">
        <!-- 未下厨 -->
        <!-- <div class="btn-item" @click="gotoInfo()">信息</div> -->
        <div class="btn-item" @click="print()">打印</div>
        <div class="btn-item" @click="cancel('judgeCance')">取消订单</div>
        <div class="btn-item" @click="Cooking()">下厨</div>
        <div class="btn-item" @click="gotoIndex()">新开单</div>
      </div>
      <div class="btn-list" v-if="footShow==-1">
        <!-- 下单失败 -->
        <!-- <div class="btn-item" @click="gotoInfo()">信息</div> -->
        <div class="btn-item" @click="print()">打印</div>
        <div class="btn-item" @click="Cooking()">下厨</div>
        <div class="btn-item" @click="gotoIndex()">新开单</div>
      </div>
      <div class="btn-list" v-if="footShow==-5">
        <!-- 已取消 -->
        <div class="btn-item" @click="print()">打印</div>
        <div class="btn-item" @click="gotoIndex()">新开单</div>
      </div>
    </div>
  </div>
</template>
<script>
import moment from "moment";
import foodHeaderNav from "../common/foodHeaderNav";
import foodPayChoise from "../common/foodPayChoise";
import { mapState, mapGetters, Store } from "vuex";
import { Indicator, Toast, MessageBox, Loadmore } from "mint-ui";
export default {
  components: {
    foodHeaderNav,
    foodPayChoise
  },
  data() {
    return {
      menuInfo: {},
      goUrl: "",
      goTitle: "",
      footShow: null,
      isShowPay: false,
      printNum: 0
    };
  },
  computed: {
    ...mapState({
      orderNum: state => state.food.orderNum,
      from: state => state.food.from
    })
  },
  created() {
    if (!this.orderNum) {
      this.$router.push("/foodIndex");
    } else {
      //console.log("this.from", this.from);
      this.getData();
      if (this.from == "shoppingCart") {
        this.goUrl = "/foodIndex";
        this.goTitle = "餐位图";
      } else if (this.from == "OrderManage") {
        this.goUrl = "/foodOrderManage";
        this.goTitle = "订单管理";
      } else if (this.from == "createOrder") {
        this.goUrl = "/foodCreateOrder";
        this.goTitle = "开单";
      }
    }
  },

  methods: {
    gotoBack() {
      this.$router.push(this.goUrl);
    },
    editMenuFun(item, index) {
      //console.log("item:", item);
      if (this.footShow == 2 || this.footShow == -1 || this.footShow == 0) {
        return;
      }
      this.menuInfo.index = index;

      this.$store.commit("submitOrderEdit", this.menuInfo);
      // console.log("editMenuFun:", this.menuInfo);
      this.$router.push("/foodOrderEdit");
    },
    gotoInfo() {
      this.$router.push("./foodInfo");
    },
    addMenu() {
      this.$router.push("./foodMenu");
    },

    cancel(style) {
      if (
        moment()
          .add(1, "hour")
          .format("YYYY-MM-DD HH:mm") > this.menuInfo.eatTime &&
        style == "judgeCance"
      ) {
        this.msg("订单不在取消时间内");
      } else {
        MessageBox.confirm("确定取消订单？")
          .then(action => {
            this.orderDealwith("x");
          })
          .catch(action => {});
      }
    },
    Cooking() {
      //this.orderDealwith(2);
      MessageBox.confirm("确认下厨？")
        .then(action => {
          this.pushOrder();
        })
        .catch(action => {});
    },
    print() {
      console.log("dayin:", this.menuInfo);
      this.printNum = 0;
      printDcData(this.printSuccess, this.printError, this.menuInfo);
    },
    pay() {
      this.isShowPay = true;
    },
    gotoIndex() {
      let seatsInfo = {
        code: this.menuInfo.tableNo,
        // descript: "2",
        // descriptEn: "2",
        pcDescript: this.menuInfo.pcDesc,
        pccode: this.menuInfo.pccode
      };

      this.$store.commit("submitSeatsInfo", seatsInfo);
      this.$router.push("/foodCreateOrder");
      //this.$router.push("./foodIndex");
    },
    hidePage(obj) {
      this.isShowPay = false;
      if (obj.state == "confirmPay") {
        // this.footShow = 0;
        // this.menuInfo.payCode = "TAIKANG-" + obj.payCode;
        // this.menuInfo.payName = obj.payName;
        // // console.log("dd----", this.menuInfo);
        // if (
        //   this.menuInfo.eatTime <
        //   moment()
        //     .add(10, "minute")
        //     .format("YYYY-MM-DD HH:mm")
        // ) {
        //   this.pushOrder();
        // }
        // this.print();
        this.payFun(obj.item);
      }
    },
    printSuccess() {
      // if (this.printNum < 1) {
      //   this.printNum++;
      //   printDcData(this.printSuccess, this.printError, this.menuInfo);
      // }
    },
    printError(v) {
      MessageBox.confirm("打印出错：" + v + ",继续打印？")
        .then(action => {
          printDcData(this.printSuccess, this.printError, this.menuInfo);
        })
        .catch(action => {});
    },
    payFun(item) {
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "updateMaster",
            accnt: this.orderNum,
            presta: 0,
            payCode: "TAIKANG-" + item.payCode,
            outId: 1,
            orderNo: 1
          },
          vv: 5
        })
        .then(res => {
          console.log("denglu:", res);
          if (res.data.code == "0") {
            this.msg("支付成功！");
            this.footShow = 0;
            this.menuInfo.payCode = "TAIKANG-" + item.payCode;
            this.menuInfo.payName = item.payName;
            if (
              this.menuInfo.eatTime <
              moment()
                .add(10, "minute")
                .format("YYYY-MM-DD HH:mm")
            ) {
              this.pushOrder();
            }
            //this.print();
          } else {
            this.msg(res.data.msg);
          }
        });
    },
    getData() {
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "getMasterInfo",
            accnt: this.orderNum
          },
          vv: 5
        })
        .then(res => {
          if (res.data.code == "0") {
            this.menuInfo = res.data.data;

            if (this.menuInfo.pluInfos.length == 0) {
              this.orderDealwith("x");
            }
            this.footShow = this.menuInfo.orderSta;
            // console.log("this.footShow:", this.footShow);

            // console.log(" this.menuInfo:", this.menuInfo);
            this.$store.commit("submitOrderInfo", this.menuInfo);
          } else {
            this.msg(res.data.msg);
          }
        });
    },
    orderDealwith(presta, payCode, orderNo, outId) {
      let params;

      if (payCode) {
        params = {
          method: "updateMaster",
          presta: presta,
          accnt: this.orderNum,
          payCode: payCode,
          orderNo: orderNo,
          outId: outId
        };
      } else {
        params = {
          method: "updateMaster",
          presta: presta,
          accnt: this.orderNum
        };
      }
      this.$http
        .get("/ohotel/router", {
          params: params,
          vv: 5
        })
        .then(res => {
          if (res.data.code == "0") {
            //this.msg(res.data.msg);

            this.menuInfo.refund = 1;
            if (
              this.footShow == 2 ||
              this.footShow == -1 ||
              this.footShow == 0
            ) {
              this.print(this.printSuccess, this.printError, this.menuInfo);
            }
            if (this.from == "OrderManage") {
              this.$router.push(this.goUrl);
            } else {
              this.footShow = -5;
            }
          } else {
            this.msg2(res.data.msg);
          }
        });
    },
    pushOrder() {
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "sendOrder",
            v: "2.0",
            accnt: this.orderNum
          },
          vv: 5
        })
        .then(res => {
          if (res.data.code == "0") {
            //this.msg(res.data.msg);
            this.msg("已下厨");
            this.$router.push(this.goUrl);
          } else {
            this.msg2(res.data.msg);
          }
        });
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    },
    msg2(msg) {
      MessageBox.alert(msg);
    }
  }
};
</script>
<style lang="less" scoped>
.foodOeder {
  .nav {
    padding: 0 0.533rem;
    background-color: #fff;
    margin-bottom: 0.267rem;
    .num-price {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0.133rem 0;
      border-bottom: 1px dashed #ddd;
      .num-content {
        text-align: left;
        border-right: 1px solid #e9ecf1;
        padding-right: 20px;
        height: 0.8rem;
        line-height: 0.8rem;
        .num-tips {
          font-size: 0.373rem;
          color: #a2a8bd;
        }
        .num {
          font-size: 0.427rem;
          color: #333333;
        }
      }
      .price-content {
        text-align: left;
        white-space: nowrap;
        .price-tips {
          font-size: 0.373rem;
          color: #a2a8bd;
        }
        .price {
          font-size: 0.64rem;
          color: #f7544d;
          .price-unit {
            font-size: 0.32rem;
          }
        }
      }
    }
    .title {
      display: flex;
      align-items: center;
      padding: 0.2rem 0;
      justify-content: space-between;
      .number {
        font-size: 0.373rem;
        text-align: left;
      }
      .restaurant {
        font-size: 0.373rem;
      }
      .orderStatus {
        font-size: 0.32rem;
        border-radius: 3px;
        padding: 2px 0.32rem;
      }
      .waitPay {
        color: #467cbb;
        background: #dbebff;
      }
      .waitCooking {
        background: #f3d824;
        color: #333333;
      }
      .cooking {
        color: #ffffff;
        background: #70a338;
      }
      .failure {
        color: #ff2233;
        background: #ffdbdf;
      }
    }
  }
  .body {
    .menu-list {
      padding: 0 0.16rem;
      .menu-item {
        display: flex;
        align-items: center;
        padding: 0.133rem 0.267rem;
        text-align: left;
        background: #ffffff;
        border-radius: 0.133rem;
        margin-bottom: 0.133rem;
        .number {
          width: 5%;
          font-size: 0.373rem;
          color: #77a0ff;
        }
        .menu-content {
          width: 45%;
          .menu-name {
            font-size: 0.427rem;
            color: #333333;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            width: 90%;
          }
          .menu-practice {
            font-size: 0.32rem;
            color: #f7544d;
            .menu-practice-chose {
              margin-right: 3px;
            }
          }
        }
        .quantity {
          width: 15%;
          font-size: 19.2px;
          color: #77a0ff;
        }
        .price-content {
          width: 30%;
          font-size: 0.64rem;
          color: #ff485f;
          margin-right: 0.427rem;
          white-space: nowrap;
          .price-unit {
            font-size: 0.32rem;
            display: inline-block;
          }
        }
        .iconfont {
          width: 5%;
        }
      }
    }
  }
  .foot {
    position: fixed;
    bottom: 0;
    width: 100%;

    background-color: #fff;

    .btn-list {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0.32rem 0.533rem;
      box-shadow: 0 -3px 4px 0 rgba(0, 0, 0, 0.1);

      .btn-item {
        width: 61px;
        height: 0.987rem;
        line-height: 0.987rem;
        background: #f47528;
        border-radius: 0.133rem;
        font-size: 0.373rem;
        color: #ffffff;

        a {
          font-size: 0.373rem;
          color: #ffffff;
        }
      }
    }
    // .btn-list-active {
    //   display: block;
    // }
  }
}
</style>



